
import React, {useRef, useEffect } from 'react';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { TWEEN } from 'three/examples/jsm/libs/tween.module.min'

const NftBanner = (width, height)=>{
  const renderer = new THREE.WebGLRenderer({antialias:true, alpha:true});
  renderer.setSize(width, height);
  renderer.outputEncoding = THREE.sRGBEncoding;
  renderer.physicallyCorrectLights = true;

  const camera = new THREE.PerspectiveCamera( 60, width / height, 1, 500 );
  camera.position.set( 40, 0, 10 );
  camera.lookAt(0,0,0);
  const scene = new THREE.Scene();
  
  const URL_PREFIX = process.env.PUBLIC_URL;  

  let postcard1:THREE.Object3D;
  let postcard2:THREE.Object3D;

  
  const init=()=>{
    addPostcard();
    addLight();
    addTween();
  }


  const addTween = () => {
    let rotation = {y:0};
    let rotateFront:TWEEN.Tween;
    const rotateBack =  new TWEEN.Tween(rotation)
      .onUpdate(() =>{
        if(postcard1) postcard1.rotation.y = Math.PI/6+rotation.y;
        if(postcard2) postcard2.rotation.y = Math.PI/6+Math.PI+rotation.y;
      })
      .to({ y: 0},1000)
      .delay (2000)
      .easing(TWEEN.Easing.Cubic.InOut)

      .onComplete(() => setTimeout(() => rotateFront.start(), 2000));

    rotateFront = new TWEEN.Tween(rotation)
      .onUpdate(() =>{
        if(postcard1) postcard1.rotation.y = Math.PI/6 + rotation.y;
        if(postcard2) postcard2.rotation.y = Math.PI/6 +Math.PI+rotation.y;
      })
      .to({ y: Math.PI }, 1000)
      .easing(TWEEN.Easing.Cubic.InOut)
      .chain(rotateBack);
    
    rotateFront.start();
  }

  const addPostcard = () => {
    const loader = new GLTFLoader();

    loader.load(`${URL_PREFIX}/nft/peace_maker_51.glb`, (gltf)=>{
      gltf.scene.scale.multiplyScalar(2);
      gltf.scene.position.y = 9;
      gltf.scene.rotation.y = Math.PI/6;
      postcard1 = gltf.scene;
      scene.add(gltf.scene);
    });

    loader.load(`${URL_PREFIX}/nft/peace_maker_1.glb`, (gltf)=>{
      gltf.scene.scale.multiplyScalar(2);
      gltf.scene.position.y = -9;
      gltf.scene.rotation.y = Math.PI/6 + Math.PI;
      postcard2 = gltf.scene;
      scene.add(gltf.scene);
    });
  }

  const addLight = () => {
    const light1  = new THREE.AmbientLight( 0xffffff, 2);
    const light2  = new THREE.DirectionalLight(0xffffff, 2);
    light2.position.set(0.5, 0, 0.866); // ~60
    scene.add(light1);
    scene.add(light2);
  }

  const render = () =>{
    renderer.render(scene, camera);
    TWEEN.update();
  }

  const animate = () => {
    render();
  }

  const getRenderer=()=>{
    return renderer;
  }

  return {init, getRenderer, animate}
}

const NftView = () => {
  const ref:any = useRef();

  useEffect(() => {
    if(!ref.current){
      return;
    }

    const width = ref.current.offsetWidth;
    const height = 600;
    const metaBanner = NftBanner(width, height);
    metaBanner.init();
    ref.current.appendChild(metaBanner.getRenderer().domElement);

    let animationNumber;
    const animate = () => {
      animationNumber = requestAnimationFrame( animate );
      metaBanner.animate();
    }

    animate();

    return () => {
        // Callback to cleanup three js, cancel animationFrame, etc
        if(ref.current){
          ref.current.innerHTML = '';
        }
        cancelAnimationFrame(animationNumber);
    }
  }, []);

  return <div ref={ref}/>;
}

export default NftView;
